import React from 'react';
import { NavLink } from 'react-router-dom';

const Header = () => {
  return (
    <nav className="main-header">
        <NavLink className="default-link" to="/atlas">atlas kolapsu</NavLink>
        <NavLink className="default-link" to="/info">o projekte</NavLink>
    </nav>
  );
}

export default Header;